.place {
	text-align: center;
	padding: 70px 0 40px;
	background: #0d0d0d url('../img/content/black-bg5.jpg');
	.title__section {
		color: #fff; }
	.justified {
		text-align: left;
		p {
			margin-bottom: 10px; }
		&__content {
			width: 100%; } } }

#map {
	position: relative;
	height: 450px;
	width: 100%; }

.takeback {
	margin-top: 20px;
	h3 {
		font-size: 24px;
		color: #fff;
		@include msb;
		&:after {
			content: none; } }
	form {
		position: relative; }
	input {
		position: relative;
		width: 60%;
		padding: 19px 50px 19px 25px;
		border: 1px solid rgba(255, 255, 255, .6);
		border-radius: 60px;
		background-color: $n;
		@include placeholder {
			text-transform: uppercase;
			color: rgba(255, 255, 255, .6); } }
	.btn {
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		max-width: 300px; } }
