.title__main {
  position: relative;
  text-transform: uppercase;
  color: #fff;
  font-size: 60px;
  line-height: 1.2em;
  margin: 0 0 .3em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
}

.title__main:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 90%;
  max-width: 480px;
  height: 8px;
  background-color: #f29c2f;
}

.title__section {
  display: inline-block;
  position: relative;
  text-transform: uppercase;
  font-size: 45px;
  color: #2f2f2f;
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
  z-index: 1;
}

.title__section span {
  position: relative;
}

.title__section span:before {
  content: '';
  position: absolute;
  top: -5px;
  right: -5px;
  display: inline-block;
  width: 25px;
  height: 25px;
  background-color: #f29c2f;
  z-index: -1;
}

.desc__y {
  font-size: 32px;
  color: #f29c2f;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 12px 20px;
  background-color: #f29c2f;
  color: #0c0c0c;
  border-radius: 4px;
  box-shadow: 0px 10px 15px 0px rgba(255, 189, 0, 0.25);
  text-transform: uppercase;
  font-size: 14px;
  transition: 0.4s;
}

.btn:hover {
  color: #fff;
  box-shadow: inset 0 0 4px rgba(255, 255, 255, 0.2), inset 0 0 4px rgba(0, 0, 0, 0.2), 0px 10px 15px 0px rgba(255, 189, 0, 0.25);
}

.btn-close {
  width: 20px;
  height: 20px;
  display: inline-block;
  position: absolute;
  top: 14px;
  right: 14px;
}

.btn-close:before {
  content: '';
  position: absolute;
  top: 3px;
  left: 4px;
  width: 18px;
  height: 2px;
  background-color: #e9e9e9;
  transform: rotate(45deg);
  transform-origin: 0 0;
}

.btn-close:after {
  content: '';
  position: absolute;
  top: 16px;
  left: 2px;
  width: 18px;
  height: 2px;
  background-color: #e9e9e9;
  transform: rotate(-45deg);
  transform-origin: 0 0;
}

.btn-close:hover {
  background-color: #ccc;
}

.btn-red {
  background-color: #94251d;
  padding: 20px 30px;
  box-shadow: 0px 10px 15px 0px rgba(148, 37, 29, 0.25);
  color: #fff;
  border-radius: 30px;
  text-align: center;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

.btn-red:hover {
  color: #f29c2f;
  box-shadow: 0px 10px 15px 0px rgba(148, 37, 29, 0.25);
}

