
.participation {
	text-align: center;
	padding: 70px 0 75px;
	background: #0d0d0d url('../img/content/black-bg5.jpg');
	&__list {
		padding: 0;
		margin: 0 0 20px;
		list-style: none;
		display: flex;
		justify-content: space-between;
		align-items: center;
		li {
			display: inline-block;
			vertical-align: top;
			width: 100%;
			max-width: 330px;
			margin-bottom: 30px; }
		h4 {
			font-size: 70px;
			line-height: .8;
			color: $yellow;
			@include mb; } }
	h3 {
		text-transform: uppercase;
		color: $red;
		@include mbl; }
	&__icon {
		display: inline-block;
		vertical-align: top;
		height: 90px;
		margin-bottom: 20px; }

	.title__section {
		color: #fff; } }
