
.result {
	position: relative;
	background: #fff url('../img/content/wh-bg.jpg');
	padding: 90px 0 60px;
	text-align: center;
	color: #000;
	&__list {
		padding: 0;
		margin: -80px 0 0;
		list-style: none;
		font-size: 0;
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			width: 20%;
			max-width: 230px;
			padding-bottom: 80px;
			counter-increment: step-counter;
			&:nth-of-type(4) {
				margin-top: 90px; }
			&:nth-of-type(3) {
				margin-top: 160px; }
			&:nth-of-type(2) {
				margin-top: 270px; }
			&:nth-of-type(1) {
				margin-top: 360px; }
			&:before {
				content: counter(step-counter)'.';
				position: absolute;
				bottom: 10px;
				left: 50%;
				@include trx(-50%);
				font-size: 73px;
				line-height: .8;
				color: $yellow;
				opacity: .4;
				@include mb; } }
		p {
			position: relative;
			font-size: 18px;
			color: #000;
			@include msb;
			border-right: 2px solid $yellow;
			padding-right: 10px;
			padding-bottom: 10px;
			border-bottom: 2px solid $yellow; } }
	&__icon {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 20px;
		height: 64px; }
	&__cr {
		margin-bottom: 35px;
		p {
			@include msb; } } }
